* {
  margin: 0;
  padding: 0;
}
ul,
li,
ol {
  list-style: none;
}
body {
  overflow: hidden;
}
#app {
  position: relative;
}
.el-table__header {
  width: 100% !important;
}
.el-table__body {
  width: 100% !important;
}
.el-input__inner {
  line-height: 30px !important;
}
.tiao {
  height: 100px;
  background: url(./assets/imgs/hole/tiao.png);
  background-position: center;
  color: #fff;
  line-height: 100px;
  font-weight: bold;
  text-indent: 20px;
}
.el {
  margin-top: 20px;
}
